<script>
import { BoltIcon, DevicePhoneMobileIcon, WalletIcon, ShoppingBagIcon } from '@heroicons/vue/24/outline'
export default {
  name: "Home",
  mounted() {
    document.querySelectorAll('a[href^="#"]').forEach(anchor => {
      anchor.addEventListener('click', function (e) {
        e.preventDefault();

        document.querySelector(this.getAttribute('href')).scrollIntoView({
          behavior: 'smooth'
        });
      });
    });

    const sections = document.querySelectorAll("section[id]");

    if (this.page === 'home') {
      window.addEventListener("scroll", navHighlighter);
    }

    function navHighlighter() {

      let scrollY = window.pageYOffset;

      sections.forEach(current => {
        const sectionHeight = current.offsetHeight;
        const sectionTop = current.offsetTop - 50;
        let sectionId = current.getAttribute("id");
        let navLink = document.querySelector(".navbar-list a[href*=" + sectionId + "]")

        if (
            scrollY > sectionTop &&
            scrollY <= sectionTop + sectionHeight
        ){
          navLink.classList.add("active");
        } else {
          navLink.classList.remove("active");
        }
      });
    }
  },
  setup() {
    const navigation = [
      { name: "Wallet", href: "https://wallet.astrocoin.uz" },
      { name: "Marketplace", href: "https://store.astrocoin.uz" },
      { name: "Student Ranks", href: "/ranks" },
    ]

    const features = [
      {
        name: 'Convenient wallet for managing coins',
        description: 'Coin monitoring, and a convenient interface for transferring and receiving coins',
        icon: WalletIcon,
      },
      {
        name: 'AstroCoin Store - with a large assortment',
        description: 'Students can also purchase various valuable gifts on the market using these coins.',
        icon: ShoppingBagIcon,
      },
      {
        name: 'Transfers are instant',
        description: 'Transfer or ask for coins from friends, and the transfer will be carried out instantly',
        icon: BoltIcon,
      },
      {
        name: 'Mobile application',
        description: 'We will soon release a mobile app for both Android and iOS devices',
        icon: DevicePhoneMobileIcon
      }
    ]

    return {
      navigation,
      features
    }
  },
}
</script>

